<template>
	<div class="body">
		<div class="search_group">
			<div class="search_group_top flex_start">
				<div class="search_box">
					<el-select v-model="short_name" clearable filterable placeholder="母/自管基金名称" @change="getListData()">
							<el-option v-for="item in all_short_name" :key="item.value" :label="item.value" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="search_box">
					<el-select v-model="short_name" clearable filterable placeholder="投资标的类别" @change="getListData()">
							<el-option v-for="item in all_short_name" :key="item.value" :label="item.value" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="search_box">
					<el-select v-model="short_name" clearable filterable placeholder="投资标的全称" @change="getListData()">
							<el-option v-for="item in all_short_name" :key="item.value" :label="item.value" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="search_box">
					<el-select v-model="short_name" clearable filterable placeholder="投资人" @change="getListData()">
							<el-option v-for="item in all_short_name" :key="item.value" :label="item.value" :value="item.value">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="search_group_bottom flex_start">
				<div class="search_box">
					<el-select v-model="name" clearable filterable placeholder="文件名称" @change="getListData()">
							<el-option v-for="item in all_name" :key="item.value" :label="item.value" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="search_box">
					<el-select v-model="fund_state" clearable  filterable placeholder="文件类型" @change="getListData()">
							<el-option v-for="item in record_state" :key="item.value" :label="item.value" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div @click="searchCompany" class="search_btn btn_box cursor">
					搜索
				</div>
			</div>
		</div>
		<div class="main under_fund">
			<div class="under_fund_header flex_between">
				<div class="">
					基金总数<span>({{total}})</span>
				</div>
				<div class="flex_start">
					<div  class="table-group-title-btn btn-blue-border">
						<span class="iconfont icon-daoru"></span>下载
					</div>
					<div  class="table-group-title-btn btn-blue">
						<span class="iconfont icon-daoru"></span>导出报告
					</div>
						<!-- <button @click="addMotherFund">
							<span class="iconfont icon-jia"></span>
							下载
						</button>
						<button @click="addMotherFund">
							<span class="iconfont icon-jia"></span>
							导出报告
						</button> -->
				</div>
				
				
			</div>
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%">
					<el-table-column v-for="(tableHeaderitem,index) of tableHeader " sortable  :key="index" :prop="tableHeaderitem.param" :label="tableHeaderitem.text" :width='tableHeaderitem.width'></el-table-column>
					<el-table-column fixed="right" label="操作" width="120">
						<template slot-scope="scope">
							<!-- <el-button @click="setUserPower(scope.row)" type="text" size="small">设置权限</el-button>
							<el-button @click="toFundRunPage(scope.row)" type="text" size="small">基金运行</el-button> -->
							<el-button @click="view(scope.row)" type="text" size="small">预览</el-button>
							<el-button @click="deleteFund(scope.row)" type="text" size="small">删除</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
				<!-- <el-pagination prev-text="上一页" next-text="下一页" background layout="prev, pager, next" :total="1000">
				</el-pagination> -->
				<el-pagination @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :total="total" :current-page="page" :page-size="pageSize"  layout="sizes, prev, pager, next, jumper" >
    			</el-pagination>
			</div>
		</div>
		<!--  设置权限弹窗开始-->
		<!-- <el-dialog title="设置可见人员" :visible.sync="isSetPowerDialogShow" width="813px" center :append-to-body='true'>
			<set-power v-bind:have-power-user-ids="havePowerUserIds" v-on:get-set-power-id="getSetPowerIds"></set-power>
			<div slot="footer" class="dialog_footer clear">
				<button class="dialog_btn dialog_btn_cancel" @click="isSetPowerDialogShow=false">取消</button>
				<button class="dialog_btn dialog_btn_submit" @click="setPowerSubmit">确定</button>
			</div>
		</el-dialog> -->
		<!--设置权限弹窗结束-->
	</div>
</template>

<script>
	import setPower from '../../components/setPower.vue'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils' 
	
	import {getHavePowerUsersApi,setPowerSubmitApi} from '../../config/api'
	import {MonterFundList,deleteMonterFund} from '../../config/apiSj'
	export default {
		data() {
			return {
				//设置权限弹窗是否显示
				isSetPowerDialogShow: false,
				havePowerUserIds:[],
				setPoweFundId:null,
				ids: '', //设置权限时选择的用户ids

				
				//搜索条件参数
				short_name:'',
				name:'',
				fund_state:'',
				BeginDate:'',
				LastDate:'',
				
				//表头数据
				tableHeader:[
					{
						param:'id',
						text:'序号',
						width:'80',
					},{
						param:'short_name',
						text:'母基金简称',
						width:'300',
					},{
						param:'name',
						text:'合伙企业',
						width:'300',
					},{
						param:'is_master',
						text:'基金类型',
						width:'120',
					},{
						param:'unit',
						text:'币种',
						width:'120',
					},{
						param:'subscription_amount',
						text:'认缴规模(万元)',
						width:'150',
					},{
						param:'amount_paid_in',
						text:'实缴规模(万元)',
						width:'150',
					},{
						param:'paid_in_proportion',
						text:'缴款比例(%)',
						width:'140',
					},{
						param:'multiple',
						text:'倍数',
						width:'150',
					},{
						param:'irr',
						text:'XIRR',
						width:'150',
					},{
						param:'external_payment',
						text:'对外认缴(万元)',
						width:'150',
					},{
						param:'external_payment_fact',
						text:'对外实缴(万元)',
						width:'150',
					},{
						param:'protocol_end_date',
						text:'协议到期日',
						width:'150',
					},{
						param:'fund_end_date',
						text:'基金期限(年)',
						width:'150',
					},{
						param:'record_state_name',
						text:'备案状态',
						width:'120',
					},{
						param:'is_change',
						text:'工商状态',
						width:'120',
					},{
						param:'check_date',
						text:'核准日期',
						width:'120',
					},{
						param:'sg_company_manager_name',
						text:'管理公司',
						width:'300',
					},
				],
				//表格数据
				tableData: [
				],

				//分页参数
				total:null,
				page:1,
				pageSize:10,

				// 下拉数据
				record_state:[],//所有基金状态
				all_short_name:[], // 所有简称
				all_name:[],// 所有名称
				
			}
		},
		created() {
			this.getListData();
		},
		mounted() {},
		components: {
			setPower
		},
		methods: {
			//删除功能
			deleteFund(row){
				if (row.auth.auth_delete) {
					let message = "确定要删除所选基金吗";
					dialogConfirmHasbtn(message).then(() => {
						deleteMonterFund({
							id:row.id,
						}).then(res => {
							if (res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								let length = this.tableData.length;
								if(length>1){
									this.getListData();
								}else{
									if(this.page>1){
										this.page = this.page - 1;
										this.getListData();
									}else{
										this.getListData(this.page);
									}
								}
							}else{
								dialogConfirm("info", res.data.message);
							}
						})
					}).catch(() => {});
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
				
			},
			//查看页面
			view(row){
				if (row.auth.check_operate) {
					this.$router.push({
						name: 'motherFundCompanyDetail',
						query: {
							id: row.id
						}
					})
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
			},
			//添加
			addMotherFund(){
				this.$router.push({
					name: "motherFundAdd"
				})
			},
			//基金运行
			toFundRunPage(row){
				if (row.auth.fund_operate) {
					this.$router.push({
						name: "motherFundRun",
						query: {
							id: row.id
						}
					})
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
				
			},




			//去设置权限
			setUserPower(row) {
				if(row.auth.auth_operate) {
					this.getHavePowerUsers(row.id);
				} else {
					dialogConfirm("info", "您没有设置权限，请联系管理员！");
				}
			},
			//去获取设置的权限数据
			getHavePowerUsers(id) {
				this.setPoweFundId = id;
				getHavePowerUsersApi({
					type: 3,
					data_id: id
				}).then(res => {
					console.log(res.data);
					if(res.data.msgNo == 1) {
						this.havePowerUserIds = res.data.payload.ids;
						this.isSetPowerDialogShow = true;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取设置权限组件返回来的ids
			getSetPowerIds(idNodes) {
				console.log(idNodes);
				this.subComponentPostIds = [];
				for(let i = 0, max = idNodes.length; i < max; i++) {
					if(this.subComponentPostIds.indexOf(idNodes[i]) == -1) {
						this.subComponentPostIds.push(idNodes[i])
					}
				}
				var postIds = this.decodeSelectedUserId(this.subComponentPostIds);
				this.ids = postIds.join(',');
			},
			//给加标识的员工id进行解码还原为id数组
			decodeSelectedUserId(data) {
				var user_ids = '';
				var selectedEmployeeId = [];
				var splitArr = [];
				for(var i = 0, max = data.length; i < max; i++) {
					splitArr = data[i].id.split('-');
					if(selectedEmployeeId.indexOf(splitArr[0]) == -1) {
						selectedEmployeeId.push(splitArr[0]);
					}
				}
				return selectedEmployeeId
			},
			//确认按钮调用的函数
			setPowerSubmit() {
				setPowerSubmitApi({
					table_name: 'mjj_fof',
					type: 3,
					data_id: this.setPoweFundId,
					ids: this.ids
				}).then(res => {
					console.log(res.data);
					if(res.data.msgNo == 1) {
						this.isSetPowerDialogShow = false;
						dialogConfirm("success", res.data.message);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			





			//获取母基金列表信息
			getListData(){
				MonterFundList({
					page: this.page,
					size:this.pageSize,
					start_protocol_end_date: this.BeginDate,
					end_protocol_end_date: this.LastDate,
					fund_state: this.fund_state,
					name: this.name,
					short_name: this.short_name,
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data  = res.data.payload;
						this.record_state = data.record_state;
						this.all_short_name = data.all_short_name;
						this.all_name = data.all_name;
						
						this.total = data.count;
						data.data.forEach((element,index) => {
							element.fund_end_date = element.fund_end_date.replace(/,/g, '+');
							if(element.is_change==1){
								element.is_change = '待变更'
							}else if(element.is_change==0){
								element.is_change = '正常'
							}
							if(element.is_master==1){
								element.is_master = '主体母基金'
							}else if(element.is_master==0){
								element.is_master = '分支母基金'
							}
							if(element.unit==1){
								element.unit = '人民币'
							}else if(element.unit==2){
								element.unit = '美元'
							}
						});
						this.tableData = data.data;
					}else{
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//搜索按钮点击事件
			searchCompany() {
				let data1 = new Date(this.BeginDate);
				let data2 = new Date(this.LastDate);
				if(data1.getTime() > data2.getTime()) {
					dialogConfirm("info", "开始时间不能大于结束时间");
					return false;
				} else {
					this.getListData()
				}
			},
			//修改页面条数时调用pageSize
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getListData()
			},
			//修该当前页数时调用currentPage
			changeCurrentPage(e){
				this.page = e;
				this.getListData()
			},
			
		},
	}
</script>

<style lang="scss" scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	.search_box{
		width: 170px;
		height: 30px;
	}
	.search_group {
		padding: 10px 0 30px 0;
		border-bottom: 1px solid #F1F1F1;
	}
	
	.search_group_bottom {
		margin-top: 20px;
	}
	
	.search_group_bottom .hover_btn {
		font-size: 14px;
	}
	
	.search_box {
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.btn_box {
		margin-left: 24px;
	}
	.under_fund_header {
		margin: 26px 0;
		color: #6292F4;
		font-size: 14px;
	}
	.under_fund_header>div{
		color: #6292F4;
	}
	// .under_fund_header span {
	// 	color: #6292F4;
	// 	padding: 0 4px;
	// }
	// .under_fund_header button{
	// 	width: 160px;
	// 	height: 30px;
	// 	color: white;
	// 	font-size: 14px;
	// 	border-radius: 20px;
	// 	background: #6292F4;
	// 	margin-left: 15px;
	// }
	// .under_fund_header button span{
	// 	color: white;
	// 	font-size: 14px;
	// }
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}

	.search_row_box .search_btn{
	    // margin-left: 76px;
	}
</style>


